.depressed {
  box-shadow: inset 1px 1px 3px #222;
  border: none;
  background-color: #a67cf5;
  margin: 5px;
  color: black; }

.beforePress {
  background-color: #c2c2dd;
  margin: 5px;
  color: black; }

.theme {
  text-transform: capitalize; }

* {
  font-family: sans-serif; }

.output-textarea .input-textarea {
  overflow-y: scroll;
  margin-left: 4px;
  transition: 0.4s; }

textarea {
  padding: 8px; }

.output-textarea {
  min-height: 200px;
  width: 100%;
  cursor: context-menu; }

.input-textarea {
  width: 100%;
  min-height: 200px;
  background-color: black;
  color: white; }

.wrong-output {
  color: #ca2d2d !important;
  font-size: 25px;
  font-weight: bold;
  border: 2px solid #f74949 !important;
  background-color: #facbcb !important; }

.outline-primary {
  background-color: #7575e0;
  border: #6666da;
  color: black; }

.outline-primary:hover {
  background-color: #7664c7; }

.ace_editor,
.ace_editor * {
  font-family: "Monaco", "Menlo", "Ubuntu Mono", "Droid Sans Mono", "Consolas", monospace !important;
  font-size: 16px !important;
  font-weight: 400 !important;
  letter-spacing: 0 !important; }

