.modal-content {
  margin-top: 100px;
  width: 500px;
  transition: 1.1s ease-out;
  box-shadow: -2rem 2rem 2rem rgba(0, 0, 0, 0.2);
  filter: blur(0);
  border: 2px solid #ccc;
  transform: scale(1);
  opacity: 1;
  visibility: visible;
  border-radius: 0px; }

.modal-header {
  border-bottom: 1px solid #ccc;
  padding: 1rem;
  margin: 0 !important;
  font-size: 1.5em;
  font-weight: bold; }

.modal-body {
  min-height: 100px;
  font-size: 20px;
  font-family: "Times New Roman", Times, serif; }

.modal-title {
  font-family: "Times New Roman", Times, serif;
  font-size: 1.5em;
  font-weight: 600;
  display: block; }

.title-header {
  margin-block-start: 0.83em;
  margin-block-end: 0.83em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  border-bottom: 1px solid #ccc;
  padding: 1rem; }

.actions {
  border-top: 1px solid #ccc;
  background: #eee;
  padding: 0.5rem 1rem; }
  .actions .okbtn {
    border: 0;
    background: #799ee2;
    border-radius: 5px;
    padding: 0.5rem 1rem;
    font-size: 0.8rem;
    line-height: 1; }

.btn-danger {
  background-color: #dc3545 !important; }

.compile {
  background-color: #eb791d !important;
  color: white; }

.color {
  border: 2px solid #60daff !important;
  background: #17181c !important;
  line-height: 1 !important;
  margin: 0.5em !important;
  padding: 10px;
  color: #60daff !important;
  transition: 0.25s; }

.color:hover,
.color:focus {
  box-shadow: 0 0.5em 0.5em -0.4em #6360ff !important;
  transform: translateY(-0.25em) !important;
  border-color: #6360ff !important;
  color: white !important; }

.select-btn {
  padding: 5px;
  padding-left: 15px;
  padding-right: 15px; }
